  color:rgb(245, 234, 234)
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(78, 76, 76);
}
li {
  float: left;
}
li a {
  display: block;
  color: rgb(179, 216, 213);
  text-align:center;
  padding: 14px 16px;
  text-decoration: none;
}
li a:hover {
  background-color: rgb(124, 126, 209);
}
